

















































































































































































































import {
  computed, defineComponent, PropType, ref,
} from '@vue/composition-api';
import {
  ProductListComponentContent,
  ProductListComponentDesign,
  ProductListComponentCpDesign,
  ProductListComponentItem,
  ProductLegalInfo,
} from '@/app/shared/client/type/component/product-list-component';
import ActionLink from '@/app/website/client/view/component/ActionLink.vue';
import ProductPrice from '@/app/website/client/view/component/ProductPrice.vue';
import StoreImage from '@/app/website/client/view/component/StoreImage.vue';
import {
  ActionLinkData,
  ActionLinkTag,
  ActionLinkType,
} from '@/app/shared/client/type/action-link';
import { isDarkColor } from '@/app/website/client/logic/color';
import useI18N, { I18nFunction } from '@/app/shared/client/logic/i18n';
import {
  getImageLayoutClass,
  getImageSizeClass,
  getProductInfoLayoutClass,
} from '@/app/website/client/logic/store-design-settings';
import { calculateVisibilityOption } from '@/app/website/client/logic/cp-design-settings';
import { ProductCardElementBehavior } from '@/app/website/client/type/cp-design-settings';
import {
  getStoreImageInfo,
  getStoreImageUrl,
  getNoImageClass, getStoreThumbnailImageUrl,
} from '@/app/website/client/logic/store-image';

interface ProductListComponentProps {
  content: ProductListComponentContent;
  design: ProductListComponentDesign;
  cpDesign: ProductListComponentCpDesign;
}

function productOnSale(product: ProductListComponentItem): boolean {
  return product.formattedComparePrice !== undefined;
}

function productLabel(
  product: ProductListComponentItem,
  i18n: I18nFunction,
): string | undefined {
  if (!product.inStock) {
    return i18n('SoldOut.label');
  }

  if (productOnSale(product)) {
    return i18n('OnSale.label');
  }

  if (product.nameYourPriceEnabled && product.zeroPrice === true) {
    return i18n('Pricing.NameYourPrice');
  }

  return undefined;
}

function calculateProductCardElementBehavior(
  hasProductImage: boolean,
  editorVisibilitySetting?: boolean,
  cpLayout?: string,
): ProductCardElementBehavior {
  return {
    show: calculateVisibilityOption(editorVisibilitySetting,
      () => cpLayout === undefined
        || cpLayout === 'SHOW'
        || cpLayout === 'HIDE'
        || (cpLayout === 'SHOW_ON_HOVER' && !hasProductImage)),
    showOnHover: calculateVisibilityOption(editorVisibilitySetting,
      () => cpLayout === 'SHOW_ON_HOVER' && hasProductImage),
  };
}

function isBuyNowEnabled(
  cpBehavior: ProductCardElementBehavior,
): boolean {
  return cpBehavior.show || cpBehavior.showOnHover;
}

function isTaxEnabled(
  legalInfos: ProductLegalInfo[] | undefined,
  buyNowEnabled: boolean,
): boolean {
  return legalInfos !== undefined && legalInfos.length > 0 && buyNowEnabled;
}

function isDetailsEnabled(
  attributeValues: string[] | undefined,
  buyNowEnabled: boolean,
): boolean {
  return attributeValues !== undefined && attributeValues.length > 0 && buyNowEnabled;
}

const ProductListComponent = defineComponent({
  components: {
    ActionLink,
    StoreImage,
    ProductPrice,
  },
  props: {
    content: {
      required: true,
      type: Object as PropType<ProductListComponentContent>,
    },
    design: {
      required: true,
      type: Object as PropType<ProductListComponentDesign>,
    },
    cpDesign: {
      required: true,
      type: Object as PropType<ProductListComponentCpDesign>,
    },
  },
  setup(props: ProductListComponentProps, { root }) {
    const i18n = useI18N(root.$store);
    const wrapRef = ref<HTMLElement>();

    const products = computed(() => props.content.products.map((product) => {
      const imageUrl = getStoreImageUrl(product);
      const thumbnailImageUrl = getStoreThumbnailImageUrl(product);
      const noImageClass = getNoImageClass(product.id, imageUrl);

      const primaryImageInfo = getStoreImageInfo(
        imageUrl,
        thumbnailImageUrl,
        product.imageBorderInfo?.dominatingColor,
        product.imageBorderInfo?.homogeneity,
      );
      const secondaryImageInfo = getStoreImageInfo(
        getStoreImageUrl(product.alternativeProductImage),
        getStoreThumbnailImageUrl(product.alternativeProductImage),
        product.alternativeProductImage?.imageBorderInfo?.dominatingColor,
        product.alternativeProductImage?.imageBorderInfo?.homogeneity,
      );

      const onSale = productOnSale(product);
      const label = productLabel(product, i18n);

      let ribbon;
      if (product.ribbon !== undefined) {
        const { red, green, blue } = product.ribbon.color;
        ribbon = {
          text: product.ribbon.text,
          color: `rgb(${red}, ${green}, ${blue})`,
          isDarkColor: isDarkColor(product.ribbon.color),
        };
      }

      const actionLinkContent: ActionLinkData = {
        title: i18n('BuyNow.button'),
        type: ActionLinkType.HYPER_LINK,
        link: product.url,
      };
      const actionLinkDesign = props.design.buyNowButton;

      return {
        id: product.id,
        name: product.name,
        formattedPrice: product.formattedPrice,
        formattedComparePrice: product.formattedComparePrice,
        url: product.url,
        primaryImageInfo,
        secondaryImageInfo,
        noImageClass,
        onSale,
        outOfStock: !product.inStock,
        label,
        actionLinkContent,
        actionLinkDesign,
        subtitle: product.subtitle,
        ribbon,
        nameYourPrice: product.nameYourPriceEnabled,
        attributeValues: product.attributeValues,
        legalInfos: product.legalInfos,
        zeroPrice: product.zeroPrice,
      };
    }));

    const isPreview = root.$store.getters.isPreviewMode;
    const productLinkTarget = isPreview ? '_blank' : '_self';
    const hasProductImage = computed(() => props.design.productImage?.visible || false);

    // дизайн настройки в CP
    const productImageSizeClass = computed(() => getImageSizeClass(props.cpDesign.imageSize));
    const productImageLayoutClass = computed(() => getImageLayoutClass(props.cpDesign.imageLayout));
    const justifyProductInfoLayout = computed(() => props.cpDesign.productInfoLayout === 'JUSTIFY');
    const productInfoLayoutClass = computed(
      () => getProductInfoLayoutClass(props.cpDesign.productInfoLayout),
    );
    const showAdditionalImage = computed(() => props.cpDesign.showAdditionalImage);
    const showCardFrame = computed(() => props.cpDesign.showCardFrame);
    const imagePosition = computed(() => props.cpDesign.imagePosition);
    const titleBehavior = computed(() => calculateProductCardElementBehavior(
      hasProductImage.value,
      props.design.productName?.visible,
      props.cpDesign.titleBehavior,
    ));
    const priceBehavior = computed(() => calculateProductCardElementBehavior(
      hasProductImage.value,
      props.design.productPrice?.visible,
      props.cpDesign.priceBehavior,
    ));
    const buyNowBehavior = computed(() => calculateProductCardElementBehavior(
      hasProductImage.value,
      props.design.buyNowButton?.visible,
      props.cpDesign.buyNowBehavior,
    ));
    const buyNowEnabled = computed(() => isBuyNowEnabled(buyNowBehavior.value));
    const hasShowOnHoverElements = computed(
      () => titleBehavior.value.showOnHover
        || priceBehavior.value.showOnHover
        || buyNowBehavior.value.showOnHover,
    );

    const navigateToLegalPage = (url: string, target: string): void => {
      if (target) {
        window.open(url, target);
      } else {
        document.location.href = url;
      }
    };

    const wrapperHasElements = computed(() => hasProductImage.value
      || titleBehavior.value.show || titleBehavior.value.showOnHover
      || priceBehavior.value.show || priceBehavior.value.showOnHover
      || buyNowBehavior.value.show || buyNowBehavior.value.showOnHover);

    return {
      wrapRef,
      products,
      hasProductImage,
      isPreview,
      productLinkTarget,
      i18n,
      buyNowEnabled,
      navigateToLegalPage,
      ActionLinkTag,
      imagePosition,
      productImageSizeClass,
      productImageLayoutClass,
      productInfoLayoutClass,
      justifyProductInfoLayout,
      showAdditionalImage,
      showCardFrame,
      hasShowOnHoverElements,
      titleBehavior,
      priceBehavior,
      buyNowBehavior,
      isTaxEnabled,
      isDetailsEnabled,
      wrapperHasElements,
    };
  },
});

export default ProductListComponent;
