


















import { computed, defineComponent } from '@vue/composition-api';

function calculatePriceClass(isOnHover: boolean): string {
  return isOnHover
    ? 'ins-component__price-hover'
    : 'ins-component__price';
}

const ProductPrice = defineComponent({
  props: {
    isOnSale: {
      required: true,
      type: Boolean,
    },
    isOnHover: {
      required: false,
      default: false,
      type: Boolean,
    },
    formattedPrice: {
      required: true,
      type: String,
    },
    formattedComparePrice: {
      required: false,
      default: undefined,
      type: String,
    },
  },
  setup(props) {
    const priceClass = computed(() => calculatePriceClass(props.isOnHover));
    return {
      priceClass,
    };
  },
});

export default ProductPrice;
