import { RGBAColor } from '@/app/shared/client/type/component/product-list-component';

export function isDarkColor({ red, green, blue }: RGBAColor): boolean {
  // HSP Color Model http://alienryderflex.com/hsp.html
  const hsp = Math.sqrt(0.299 * red ** 2 + 0.587 * green ** 2 + 0.114 * blue ** 2);
  return hsp > 200;
}

export default {};
